/* modal styles */
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  .backdrop img {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  }
  
  @media screen and (max-width: 450px) {
    .backdrop img {
      margin: 8px auto;
      max-width: 98%;
      max-height: auto;
      padding: 0.5rem;
    }
  }
  